.ramos-sentence {
  height: auto;
  width: 90%;
  margin: -10px 50px 20px 20px;
}

.banner-sentence {
  height: auto;
  width: 100%;
}

.ramos-section {
  padding: 70px 50px 50px 50px;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.ramo-avezinha {
  background-color: #FCE614;
  color: #154A7F;
}
.ramo-aventura {
  background-color: #277A4A;
  color: white;
}
.ramo-caravela {
  background-color: #D27123;
  color: white;
}
.ramo-moinho {
  background-color: #0033a1;
  color: white;
}
.dirigente {
  background-color: #AD2518;
  color: white;
}

.ramos-text {
  width: 100%;
  opacity: 0.8;
  -webkit-column-count: 2;
          column-count: 2;
  margin: 0px 30px;
  background-color: transparent !important;
}

.ramos-text p {
  margin: 0px 60px 16px 60px;
  font-size: 16px !important;
  font-family: 'Poppins' !important;
  background-color: transparent !important;
  color: inherit !important;
}

.ramos-text p span {
  font-family: 'Poppins' !important;
  font-size: 16px !important;
  background-color: transparent !important;
  color: inherit !important;
}

.ramos-buttons-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
}

.ramos-button {
  border-style: none;
  border-radius: 50px;
  font-size: 20px;
  padding: 6px 12px;
  outline: none !important;
  margin-top: 40px;
}

.blue-border {
  border: 1px solid #154A7F !important;
}

.white-border {
  border: 1px solid white !important;
}

.ramos-button:hover {
  font-weight: 700;
  background-color: white;
  opacity: 0.7;
  color: #154A7F;
}

.ramos-bold {
  font-weight: 600;
}

@media(max-width: 768px) {
  .ramos-image {
    min-height: 130px;
  }
  .ramos-image img {
    height: 130px;
  }
  .ramos-text {
    -webkit-column-count: 1;
            column-count: 1;
    margin: 0px 10px;
    font-size: 13px !important;
  }
  .ramos-text p, .ramos-text p span {
    font-size: 13px !important;
  }
  .ramos-section {
    padding: 50px 50px 50px 50px;
  }
  .ramos-text p {
    margin: 0px 0px 16px 0px;
  }
  .ramos-side {
    width: 85%;
  }
  .ramos-button {
    font-size: 16px;
  }
}
