.QueresSerGuia {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  padding-top: 80px;
  padding-bottom: 100px;
}

.guia-megafone {
  width: 60%;
  height: 60%;
}

.guia-text-section {
  width: 250px;
  text-align: center;
}

.guia-title {
  text-transform: capitalize;
  margin: 20px 0px;
}

.guia-text {
  margin-bottom: 40px;
  opacity: 0.8;
  font-weight: 500;
}

.guia-form-section form {
  max-width: 500px;
  padding: 40px 40px 20px 40px;
  border-top: 2px solid #5ea4f3;
  background-color: white !important;
  box-shadow: 0 2px 10px rgba(0,0,0,.075);
}

.guia-label {
  margin-bottom: 5px;
}

.guia-form-section input {
  width: 420px;
  margin-bottom: 10px;
}

.recolha-dados-text {
  font-size: 10px;
  text-align: justify;
  line-height: 18px;
  margin-top:20px;
}

.form-button-section {
  margin: 30px 0px 10px 0px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.guia-form-button {
  box-shadow: 0 3px 20px 0 #000000;
  background-color: #004982;
  border-radius: 30px;
  color: white;
  font-weight: 500;
  border: 1px solid transparent;
  outline: none !important;
  padding: 6px 15px;
  font-size: 14px;
  font-family: 'Montserrat', sans-serif !important;
  margin-bottom: 20px;
}

.guia-form-button:hover {
  font-weight: bold;
  background-color: #007bff !important;
  opacity: 0.7;
}

.form-error {
  color: red;
  font-size: 10px;
  margin-top: -10px;
}

.message-error {
  color: red;
  font-size: 10px;
  margin-top: 0px;
}

.alert-section {
  display: flex;
}

.form-alert {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 15px;
  margin: 0 auto;
  font-size: 12px;
  position: fixed;
  bottom: 0px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.message-icon {
  margin-right: 5px;
}

@media(max-width: 768px) {
  .QueresSerGuia {
    padding-top: 50px;
    padding-bottom: 50px;
  }
  .guia-form-section {
    width: 340px;
  }
  .guia-form-section input {
    width: 280px;
    font-size: 14px !important;
  }
  .guia-form-section form {
    padding: 40px 30px 20px 30px;
  }
  .guia-label {
    margin-bottom: 0px;
    font-size: 14px;
  }
  .guia-megafone {
    width: 35%;
    height: 35%;
    margin-top: -10px;
  }
  .form-alert {
    font-size: 10px;
  }
  .recolha-dados-text {
    margin-top: 15px;
    line-height: 16px;
  }
}

@media(max-width: 768px) {
  .guia-form-section {
    width: 300px;
  }
  .guia-form-section input {
    width: 240px;
    font-size: 16px;
  }
}