.RamosDirigente {
  margin-top: 170px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
}

.ramos-photos-banner {
  display: flex;
  width: 100%;
  margin-bottom: 20px;
}

.ramos-image {
  display: flex;
  width: 33%;
  max-height: 300px;
}

.ramos-image img {
  width: 100%;
  object-fit: cover;
  object-position: center;
}

.ramos-image-margin {
  margin: 0px 10px 0px 10px
}

